import { Component, Input } from '@angular/core';
/**
 * Componente card container
 * Contenedor dinámico para uso en distintos componentes.
 *
 * @export
 * @class BcCardContainerComponent
 */
@Component({
  selector: 'bc-card-container',
  templateUrl: 'bc-card-container.component.html',
})
export class BcCardContainerComponent {
  classTypeCard = {
    default: 'bc-card-container',
    'stroke-1': 'bc-card-container-stroke',
    'stroke-2': 'bc-card-container-stroke-2',
  };

  /**
   * Parámetro para denotar el tipo de card container
   *
   * @type {('default' | 'stroke-1' | 'stroke-2')}
   * @memberof BcCardContainerComponent
   */
  @Input() cardType: 'default' | 'stroke-1' | 'stroke-2' = 'default';

  /**
   * Parámetro para denotar el tamaño del borde
   *
   * @type {string}
   * @memberof BcCardContainerComponent
   */
  @Input() borderClass: string;

  /**
   * Parámetro para denotar el shadow
   *
   * @type {string}
   * @memberof BcCardContainerComponent
   */
  @Input() shadowClass: string;

  /**
   * Parámetro para denotar el color
   *
   * @type {string}
   * @memberof BcCardContainerComponent
   */
  @Input() cardColorClass: string;

  /**
   * Parámetro para denotar el color del borde
   *
   * @type {string}
   * @memberof BcCardContainerComponent
   */
  @Input() borderColorClass: string;

  /**
   * Parámetro para agregar una class personalizada
   *
   * @type {string}
   * @memberof BcCardContainerComponent
   */
  @Input() otherClass: string;

  setCardTypeClass() {
    return this.classTypeCard[this.cardType];
  }
}
